body {
  font-family: Arial, Helvetica, sans-serif;
 
  /*background-image:  url("er.jpg") no-repeat fixed center; */
  
}


/* Full-width input fields to form*/
input[type=text], input[type=password]{
  width: 100%;
  padding: 15px;
  border: none;
  background: #f1f1f1;
  color:black;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
  color:black;
}
/*form email*/
input[type=email], input[type=password]{
  width: 100%;
  padding: 15px;
  border: none;
  background: #f1f1f1;
  color:black;
}

input[type=email]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
  color:black;
}

/*for date*/
input[type=date], input[type=password]{
  width: 100%;
  padding: 15px;
  border: none;
  background: #f1f1f1;
  color:black;
}

input[type=date]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
  color:black;
}
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/*  style for registerbtn */
.registerbtn {
  background-color: #4CAF50 ;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
 
}
/*card*/
.card{
	box-shadow:0 4px 8px 0 rgba (0,0,0,0.2);
	max-width:400px;
	margin:auto;
	text-align:center;
  font-family:arial;
 /*background-image:  url("img8.jpg") ;*/
	color:black;
  	border-radius: 5px;
  	padding: 20px;
  	 background-color:#54d8737c;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

.registerbtn:hover {
  opacity: 1;
}



/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}




  
 